@charset "UTF-8";
[data-theme=intellore-theme] {
  /* Base background & text */
  --bg-color: #edf2fb; /* page background */
  --text-color: #1a1a1a;
  --text-muted: #4b4b4b;
  /* Primary color palette */
  --primary: #abc4ff;
  --primary-color: var(--primary);
  --primary-light: #c1d3fe;
  --primary-dark: #89a8ff;
  --primary-color-hover: #89a8ff;
  /* Secondary & accent colors */
  --secondary: #b6ccfe;
  --accent-color: #ccdbfd;
  /* Card / container backgrounds */
  --card-bg: #e2eafc;
  --card-bg-hover: #d7e3fc;
  --card-border-color: #b6ccfe;
  /* Table / list backgrounds */
  --table-bg: var(--bg-color);
  --table-hover-bg: #c1d3fe;
  --table-header-bg: #c1d3fe;
  /* Navbar / header */
  --navbar-bg: #d7e3fc;
  --navbar-text-color: #1a1a1a;
  /* Sidebar */
  --sidebar-bg: #abc4ff;
  --sidebar-text-color: #1a1a1a;
  /* Forms & inputs */
  --input-bg: #edf2fb;
  --input-border: #b6ccfe;
  --input-focus-border: #89a8ff;
  /* Buttons */
  --btn-primary-bg: var(--primary);
  --btn-primary-hover-bg: var(--primary-dark);
  --btn-border-radius: 6px;
  /* Shadows */
  --shadow-sm: 0 1px 2px rgba(0, 0, 0, 0.05);
  --shadow-md: 0 2px 6px rgba(0, 0, 0, 0.06);
}

/* Optional: make sure body uses the theme variables */
[data-theme=intellore-theme] body {
  background-color: var(--bg-color) !important;
  color: var(--text-color) !important;
}

/* Navbar */
[data-theme=intellore-theme] .navbar,
[data-theme=intellore-theme] .frappe-navbar {
  background-color: var(--navbar-bg) !important;
  color: var(--navbar-text-color) !important;
}

/* Sidebar */
[data-theme=intellore-theme] .layout-side-section,
[data-theme=intellore-theme] .sidebar {
  background-color: var(--sidebar-bg) !important;
  color: var(--sidebar-text-color) !important;
}

/* Cards & boxes */
[data-theme=intellore-theme] .card,
[data-theme=intellore-theme] .dashboard-graph,
[data-theme=intellore-theme] .dash-box,
[data-theme=intellore-theme] .frappe-card {
  background-color: var(--card-bg) !important;
  border: 1px solid var(--card-border-color) !important;
  box-shadow: var(--shadow-sm) !important;
}

/* Table headers */
[data-theme=intellore-theme] table thead {
  background-color: var(--table-header-bg) !important;
}

/* Form sections */
[data-theme=intellore-theme] .form-layout,
[data-theme=intellore-theme] .form-dashboard-section,
[data-theme=intellore-theme] .form-section,
[data-theme=intellore-theme] .grid-body {
  background-color: var(--card-bg) !important;
}

[data-theme=forest-theme-1] {
  --primary-bg: #22273c; /* Workspace background */
  --sidebar-bg: #181824; /* Sidebar/nav background */
  --accent-green: #3b86d1; /* Primary accent/actions */
  --header-bg: #3b86d1; /* Section/page headings */
  --accent-blue: #3b86d1; /* Buttons/links */
  --content-bg: #f4f6fa; /* Cards, forms, dashboards */
  --card-bg: #ffffff; /* Widget boxes/modal content */
  --text-dark: #1e1e1e; /* Main text in content */
  --text-light: #fff; /* Sidebar/nav text */
  --neutral-border: #d0d2d3; /* Card borders, dividers */
  --highlight: #ffe082; /* CTA, highlights */
  --business-gradient-primary: linear-gradient(135deg, #1a365d 0%, #2b6cb0 100%);
  --business-primary-light: #2d5a87;
  --business-primary-dark: #0f2a44;
  --business-secondary: #2b6cb0;
  --business-accent: #3182ce;
  --business-success: #38a169;
  --business-warning: #d69e2e;
  --business-danger: #e53e3e;
  --business-gray-50: #f7fafc;
  --business-gray-100: #edf2f7;
  --business-gray-200: #e2e8f0;
  --business-gray-300: #cbd5e0;
  --business-gray-400: #a0aec0;
  --business-gray-500: #718096;
  --business-gray-600: #4a5568;
  --business-gray-700: #2d3748;
  --business-gray-800: #1a202c;
  --business-gray-900: #171923;
  --business-gradient-primary: linear-gradient(135deg, #1a365d 0%, #2b6cb0 100%);
  --business-gradient-secondary: linear-gradient(135deg, #2d5a87 0%, #3182ce 100%);
  --business-gradient-card: linear-gradient(145deg, #ffffff 0%, #f7fafc 100%);
  --business-shadow-sm: 0 1px 3px rgba(26, 54, 93, 0.12);
  --business-shadow-md: 0 4px 6px rgba(26, 54, 93, 0.15);
  --business-shadow-lg: 0 10px 15px rgba(26, 54, 93, 0.15);
  --business-shadow-xl: 0 20px 25px rgba(26, 54, 93, 0.15);
  --business-transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  --business-transition-fast: all 0.15s ease-in-out;
}

