[data-theme=forest-theme-1] .dashboard-graph,
[data-theme=forest-theme-1] .dash-box,
[data-theme=forest-theme-1] .form-layout,
[data-theme=forest-theme-1] .form-column.col-sm-12,
[data-theme=forest-theme-1] .card,
