}

[data-theme=forest-theme-1] .dropdown-list {
  background-color: var(--business-primary-dark);
}

[data-theme=forest-theme-1] .btn:hover,
[data-theme=forest-theme-1] .btn:focus {
  background: var(--accent-green);
  color: var(--text-light);
}

/* Inputs and forms */
[data-theme=forest-theme-1] input.form-control,
[data-theme=forest-theme-1] textarea.form-control,
[data-theme=forest-theme-1] select.form-control {
  background: var(--card-bg);
  color: var(--text-dark);
