}

/* Table headers */
[data-theme=forest-theme-1] table thead {
  background: var(--content-bg);
  color: var(--primary-bg);
  border-bottom: 1px solid var(--neutral-border);
}

/* CTAs/highlight blocks */
[data-theme=forest-theme-1] .contact-cta,
[data-theme=forest-theme-1] .cta-section {
  background: var(--highlight) !important;
  color: var(--primary-bg) !important;
  border-radius: 10px;
}

/* Modals/dialogs body and footer */
[data-theme=forest-theme-1] .modal-content {
  background: var(--card-bg);
}

[data-theme=forest-theme-1] .modal-body,
[data-theme=forest-theme-1] .modal-footer {
  background: var(--content-bg);
  color: var(--text-dark);
}

/* Timeline items */
[data-theme=forest-theme-1] .timeline-items,
[data-theme=forest-theme-1] .timeline-items a {
