  font-weight: normal;
  padding: 8px 12px; /* optional for spacing */
  text-decoration: none;
}

[data-theme=forest-theme-1] {
  --icon-stroke: #ffffff; /* white color for SVG icons */
}

/* Hover state: background only, no text color change */
