}

/* Active/selected state */
[data-theme=forest-theme-1] .list-row.active {
  background-color: rgba(59, 134, 209, 0.25); /* slightly stronger overlay */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* stronger shadow */
  transform: translateY(-1px);
}

/* Smooth transition for hover/active effects */
[data-theme=forest-theme-1] .list-row {
  transition: background-color 0.2s ease, box-shadow 0.2s ease, transform 0.2s ease;
}

/* Target your page-form container */
[data-theme=forest-theme-1] .page-form.flex {
  border-radius: 12px; /* curved edges */
  box-shadow: 0 4px 12px rgba(34, 39, 60, 0.07); /* subtle shadow for depth */
